import React from 'react';
const Search = props => {
  const getStyle = () => {
    const { showSearch } = props;
    let styles = {
      search: {
        transform: showSearch ? 'translateX(-25%)' : 'translateX(0%)',
        transition: `transform ${
          showSearch ? '0.33s' : '0.13s'
        } cubic-bezier(0,0,0.3,1)`
      }
    };

    return styles;
  };
  return (
    <React.Fragment>
      <form className="form-inline" style={getStyle().search}>
        <div style={{ display: props.showSearch ? 'block' : 'none' }}>
          <input
            placeholder="Search.."
            className="form-control form-control-sm"
            size="10"
          />
        </div>
      </form>
    </React.Fragment>
  );
};

export default Search;
